import React from "react";
import { useHistory } from "react-router-dom";
import styles from "./About.module.css";
import Button from "components/UI/Button/Button";

const About = (props) => {
  const history = useHistory();

  const buttonClickHandler = () => {
    history.push("/services");
  };

  return (
    <section className={`${styles.about} ${props.className}`}>
      <div className={styles.top}>
        <h1>O nas</h1>
        <h3>
          Salon barberski Barber Shop to miejsce ktre powstao z myl o
          Twoim zarocie. Nasz zesp tworz zgrani i dowiadczeni stylici,
          ktrzy cay czas podnosz swoje kwalifikacje zawodowe. Kompleksowo
          zadbaj o kady wos, aby speni wszystkie oczekiwania.
        </h3>
      </div>
      <div className={`${styles.bottom}`}>
        <div className={styles.left}>
          <h3>Ju teraz skorzystaj z naszej oferty</h3>
        </div>
        <div className={styles.right}>
          <Button type="button" onClick={buttonClickHandler}>
            Umw wizyt
          </Button>
        </div>
      </div>
    </section>
  );
};

export default About;
