import React from "react";
import styles from "./Services.module.css";
import trim from "./img/trim.jpg";
import haircut from "./img/haircut.jpg";
import procedure from "./img/procedure.jpg";
import barber from "./img/barber.jpg";
import products from "./img/products.jpg";

const Services = (props) => {
  return (
    <section className={`${props.className} ${styles.services}`}>
      <h1>Nasze usugi</h1>
      <ul>
        <li>
          <img src={trim} alt="Zarost zdjcie" className={styles.image} width="100%" height="auto" />
          <div className={styles["service-description"]}>
            <h3>Strzyenie i modelowanie brody</h3>
            <br />
            <p>
              Strzyenie i modelowanie brody w naszym to salonie to najlepszy
              wybr, aby zadba o swoj brod.
            </p>
          </div>
        </li>
        <li>
          <div className={styles["service-description"]}>
            <h3>Strzyenie i stylizowanie fryzury</h3>
            <br />
            <p>
              Kadego klienta traktujemy indywidualnie, pomagamy dobra fryzur
              w zalenoci od typu urody, rodzajw wosw oraz panujcych
              trendw.
            </p>
          </div>
          <img src={haircut} alt="Fryzura zdjcie" className={styles.image} width="100%" height="auto" />
        </li>
        <li>
          <img src={procedure} alt="Zabiegi zdjcie" className={styles.image} width="100%" height="auto" />
          <div className={styles["service-description"]}>
            <h3>Zabiegi pielgnacyjne</h3>
            <br />
            <p>
              Pielgnacja zarostu jest bardzo wana dla zachowania
              nieskazitelnego wygldu.
            </p>
          </div>
        </li>
        <li>
          <div className={styles["service-description"]}>
            <h3>Doradztwo w zakresie pielgnacji i stylizacji zarostu</h3>
            <br />
            <p>
              Masz kilka pyta? Nasi pracownicy peni wiedzy odpowiedz na nie.
            </p>
          </div>
          <img src={barber} alt="Doradztwo zdjcie" className={styles.image} width="100%" height="auto" />
        </li>
        <li>
          <img src={products} alt="Produkty zdjcie" className={styles.image} width="100%" height="auto" />
          <div className={styles["service-description"]}>
            <h3>Produkty pielgnacyjne</h3>
            <br />
            <p>
              Zakup potrzebne produkty do pielgnacji twojej brody w domu.
            </p>
          </div>
        </li>
      </ul>
    </section>
  );
};

export default Services;
